import Header from '../components/Header'
import Footer from '../components/Footer'
import React, { useState } from 'react'
import '../css/contact.css'
import Axios from 'axios'
import { useParams, useNavigate } from 'react-router-dom'
import img from '../images/waterfall.png'

export default function Question03() {
  const [studentid, setStudentid] = useState('');
  const [answer, setanswer] = useState('');
  const { id } = useParams();
  const navigate = useNavigate();
  const qid = id;

  const submitSignup = () =>{
    Axios.post('http://localhost:3001/api/q3',{
        qid:qid,
        studentid:studentid,
        answer:answer
    }).then(()=>{
        alert("signup succesfully !");
        navigate("/")

    });
}
  return (
    <>
      <Header />
      <div className="App container mt-4">
        <h1>  </h1>
        <div className='form'>
          <div className="mb-3">
            <label className='form-label'> </label>
            <input
              type='text'
              value={id} // Use value attribute to set value
              disabled // Disable editing
            />
          </div>

          <div className="mb-3">
            <label className='form-label'> </label>
            <input
              type='text'
              className='form-control'
              name='studentid'
              value={studentid}
              onChange={(e) => {
                setStudentid(e.target.value);
              }}
            />
          </div>

          <div className="mb-3">
          <img src={img} alt=''></img>
            <label className="form-label">        </label>
            <textarea name="message" id="message"  className="form-control" onChange={(e) => {
                setanswer(e.target.value);
              }}></textarea>
          </div>

          
        </div>
        <button className='btn btn-primary mt-3' onClick={submitSignup}>Submit</button>
      </div>
      <Footer />
    </>
  )
}
