import Header from '../components/Header'
import Footer from '../components/Footer'
import React from 'react'
import { useState } from 'react'
import '../css/contact.css'
import Axios from 'axios'
import { useParams, useNavigate } from 'react-router-dom'



export default function Question02()
{
    
    const { id } = useParams();
    const navigate = useNavigate();
    const qid = id;

   
    const[studentid, setStudentid]=useState('')
    
    const submitSignup = () =>{
      Axios.post('http://localhost:3001/api/submission',{
          qid:qid,
          studentid:studentid
      }).then(()=>{
          alert("signup succesfully !");
          navigate("/")

      });
  }


    return(
        <>
        <Header/>
        <div className="App container mt-4">
      <h1>  </h1>
      <div className='form'>
        <div className="mb-3">
          <label className='form-label'> </label>

          <input 
          type='text'
          value={id} // Use value attribute to set value
          disabled // Disable editing
         
        />
        </div>

        <div className="mb-3">
          <label className='form-label'> </label>
          <input type='text' className='form-control'
          name='studentid' onChange={(e)=>{
                    setStudentid(e.target.value);
                }}
           />
        </div>

        <label className='form-label'> </label>
        <input type='text' className='form-control'
          name='studentid' 
           />
        <form className="mb-3">
          <label className="form-label">   </label>
          <input type="file" className="form-control-file" />
          <button type="submit" className="btn btn-primary mt-2">Upload</button>
        </form>

        <label className='form-label'> </label>
        <input type='text' className='form-control'
          name='studentid' 
           />
        <form className="mb-3">
          <label className="form-label">   </label>
          <input type="file" className="form-control-file" />
          <button type="submit" className="btn btn-primary mt-2">Upload</button>
        </form>

        <label className='form-label'> </label>
        <input type='text' className='form-control'
          name='studentid' 
           />
        <form className="mb-3">
          <label className="form-label">   </label>
          <input type="file" className="form-control-file" />
          <button type="submit" className="btn btn-primary mt-2">Upload</button>
        </form>

        
      </div>
      <button className='btn btn-primary mt-3'onClick={submitSignup}>Submit</button>
    </div>
        <Footer/>
        </>
    )
}